"use strict";
var index_controller_1 = require('./ctls/index.controller');
exports.initRouter = function ($urlRouterProvider, $stateProvider) {
    $stateProvider.state("home.tty", {
        url: "tty",
        data: {
            permissions: {
                except: ["anonymous"],
                only: ["user"]
            }
        },
        views: {
            "content": {
                controller: index_controller_1.TtyController,
                controllerAs: "ttyCtl",
                template: require("./tpls/index.template.jade")()
            }
        }
    });
};



